import Vue from 'vue';
import Vuetify from 'vuetify/lib';
import colors from 'vuetify/lib/util/colors'

Vue.use(Vuetify);

export default new Vuetify({
   theme: {
      dark: false,
      themes: {
        light: {
          primary: colors.blueGrey.darken1, // #E53935
          secondary: colors.amber.darken1, // #FFCDD2
          button: colors.amber.darken1
        },
        dark: {
          primary: "#663B0C",
          secondary: "#2F2010",
        }
      },
    },
});
